.btn-hover {
  transition: background-color var(--time-transition);
}

.btn-hover:hover, .btn-hover:focus {
  background-color: var(--hover-button);
}

.nav-hover {
  color: #fff;
  transition: color var(--time-transition);
}

.nav-hover:hover, .nav-hover:focus {
  color: var(--primary-color);
}

.menu-container {
  z-index: 6;
  width: 40%;
  min-width: 250px;
  height: 100vh;
  transition: transform var(--time-transition);
  background-color: #e17992;
  padding: 60px 0;
  position: fixed;
  right: 0;
  transform: translateX(100%);
}

.menu-container.is-open {
  transform: translateX(0);
}

.menu-close-btn {
  width: 20px;
  height: 20px;
  cursor: pointer;
